/*	Animation effects
/*-----------------------------------------------------------------------------------*/
.b-animate-go {
  text-decoration: none;
}

.b-animate {
  transition: all 0.5s;
  -moz-transition: all 0.5s;
  -ms-transition: all 0.5s;
  -o-transition: all 0.5s;
  -webkit-transition: all 0.5s;
  font-size: 1.1em;
  font-weight: 700;
}

.b-animate img {
  margin-top: 4%;
  display: -webkit-inline-box;
}

.b-animate span {
  font-family: 'Lato', sans-serif;
  display: block;
  font-size: 1.4em;
  text-transform: uppercase;
  padding-top: 2.2em;
  display: block;
  font-weight: 600;
  width: 81%;
  margin: 0 auto;
  text-shadow: 1px 1px #000000, -1px -1px #000000, -1px 1px #000000, 1px
    -1px #000000;
}

.b-animate button {
  font-family: 'Lato', sans-serif;
  border: 3px solid #fb7802;
  padding: 18px 1em;
  color: #fff;
  font-weight: 600;
  font-size: 23px;
  background: none;
  margin: 0.8em auto;
  transition: 0.5s all;
  -webkit-transition: 0.5s all;
  -moz-transition: 0.5s all;
  -ms-transition: 0.5s all;
  -o-transition: 0.5s all;
  outline: none;
  margin-top: 20%;
  text-shadow: 1px 1px #000000, -1px -1px #000000, -1px 1px #000000, 1px
    -1px #000000;
}

.b-animate label {
  display: block;
  font-size: 0.875em;
  margin-top: 0.5em;
  transition: 0.5s all;
  -webkit-transition: 0.5s all;
  -moz-transition: 0.5s all;
  -ms-transition: 0.5s all;
  -o-transition: 0.5s all;
}

.b-animate label:hover {
  color: #000;
  cursor: pointer;
}
/* lt-ie9 */
.b-animate-go:hover .b-animate {
  visibility: visible;
}

